import React, { Component } from 'react';
import * as firebase from 'firebase';

class OrderBookEntry extends Component {

  constructor(){
    super();
    this.state = {
      placeholderBid:"",
      placeholderAsk:""
    };
  }
  handleClear = (event) => {
  event.persist();
  const ordersRef = firebase.database().ref("Sessions").child(this.props.sessionid).child("Users").child(this.props.uid).child("Orders").child("INTC").child((this.props.Strike*100).toFixed(0));
  const deleteRef = firebase.database().ref("Sessions").child(this.props.sessionid).child("Users").child(this.props.uid).child("DeletesPending");
    ordersRef.once('value', snap =>{
      let rawOrders = snap.val();
      if(rawOrders != null){
      //console.log(rawOrders);
      let keys = Object.keys(rawOrders);
      for(var i=0;i<keys.length;i++){
        if(rawOrders[keys[i]].Type==='Bid' && event.target.name==='userBidReset'){
          //ordersRef.child(keys[i]).child("DeleteThis").set(true);
          let newKey = deleteRef.push();
          newKey.set(rawOrders[keys[i]]);
        }else if(rawOrders[keys[i]].Type==='Ask' && event.target.name==='userAskReset'){
          //ordersRef.child(keys[i]).child("DeleteThis").set(true);
          let newKey = deleteRef.push();
          newKey.set(rawOrders[keys[i]]);
        }
      }
     }
    });

    if(event.target.name === 'userBidReset'){
      //console.log("Clear Bid at "+(this.props.Strike*100).toFixed(0));
    }else{
      //console.log("Clear Ask at "+(this.props.Strike*100).toFixed(0))
    };
  }
  handleKeyUp = (event) => {
  let totVol = 0;
  const ordersPendingRef = firebase.database().ref("Sessions").child(this.props.sessionid).child("Users").child(this.props.uid).child("OrdersPending");
  //needs updating
  const aheadRef = firebase.database().ref("Sessions").child(this.props.sessionid).child("Users").child(this.props.uid).child("Orders").child("INTC").child((this.props.Strike*100).toFixed(0));
    if(event.key === 'Enter'){
      if(event.target.value != null && !isNaN(event.target.value) && event.target.value % 1 === 0){
      var volumeInt = parseInt(event.target.value);
      if(volumeInt > 0 && volumeInt <= 999){
      if(event.target.name==='userBid'){

        var order = {
          AheadOfThis: 0,
          OrderId: -1,
          UserId: this.props.uid,
          Type: event.target.getAttribute("data-type"),
          Volume: volumeInt,
          AssetId: "INTC",
          Strike: (this.props.Strike*100).toFixed(0),
          Class: "Limit"

        }
        //console.log(order);
        event.target.value = "";
        var newOrderRef = ordersPendingRef.push();
        newOrderRef.set(order);
        aheadRef.on('value', snap =>{
           var rawData = snap.val();
           //console.log(rawData); //strikes
           for(var key in rawData){
             if(rawData[key].Type === "Bid" && rawData != null){
             totVol += rawData[key].Volume;
             //console.log(totVol);
             this.setState({
               placeholderBid:(totVol).toString()+" ("+(rawData[key].AheadOfThis).toString()+")",
               placeholderAsk:""
             });
             //console.log(rawData[key]);
            }
           }

         })
      }else{

        var order = {
          AheadOfThis: 0,
          OrderId: -1,
          UserId: this.props.uid,
          Type: event.target.getAttribute("data-type"),
          Volume: volumeInt,
          AssetId: "INTC",
          Strike: (this.props.Strike*100).toFixed(0),
          Class: "Limit"
        }
        //console.log(order);
        event.target.value = "";
        var newOrderRef = ordersPendingRef.push();
        newOrderRef.set(order);
        aheadRef.on('value', snap =>{
           var rawData = snap.val();
           //console.log(rawData);
           for(var key in rawData){
             if(rawData[key].Type === "Ask"){
             this.setState({
               placeholderBid:"",
               placeholderAsk:(volumeInt).toString()+" ("+(rawData[key].AheadOfThis).toString()+")"
             });
             //console.log(rawData[key].AheadOfThis);
           }
         }
        })
      }
    }else{
      alert("You must enter a number between 0 and 999");
      event.target.value = "";
    }
    }else{
      alert("You must enter a valid whole number");
      event.target.value = "";
    }
    };
  };
  render(){
    return (
      <div>
        <div className='col-md-3'><input onKeyUp={this.handleKeyUp} data-type="Bid" name="userBid" className="userBid" type="text" size="5"  />
        <input onClick={this.handleClear} className="btn-danger" name="userBidReset" type="reset" size="4" value="Clear" /></div>
        <div className='col-md-2 bid'>{this.props.Bid}</div>
        <div className='col-md-2 strike'>{this.props.Strike}</div>
        <div className='col-md-2 ask'>{this.props.Ask}</div>
        <div className='col-md-3' ><input onClick={this.handleClear} className="btn-danger" name="userAskReset"  type="reset" size="4" value="Clear" />
        <input onKeyUp={this.handleKeyUp} data-type="Ask" className="userAsk" type="text" size="5"  />
        </div>
      </div>
    );
  }
}
export default OrderBookEntry;



