import React, { Component } from 'react';
import Header from './Headers';
import OrderBookEntry from './OrderBookEntry';
import MarketOrders from './MarketOrders';
import * as firebase from 'firebase';


class PlaceOrder extends Component {


  constructor(props){
    super(props);
    this.state = {
      orders: [],
      sessionid:'',
      uid: this.props.uid,
      ready: false
    };
  }


  componentDidMount(){
    //console.log(this.state.uid);
    this.aggobRef = firebase.database().ref("Sessions").child(this.props.sessionid).child("Assets").child("INTC").child("AggOB");

    var strike, bid, ask;
    //var rawStrike;
     this.aggobRef.on('value', snap =>{
       var rawData = snap.val();
       if(rawData){
       //console.log(rawData);
       var ords = [];
       for(var key in rawData){
          //rawStrike = key;
          strike = (key/100).toFixed(2);
          bid = rawData[key].Bid;
          if(bid === undefined){
            bid = 0;
          }
          ask = rawData[key].Ask;
          if(ask === undefined){
            ask = 0;
          }
          if(bid === 0){
            bid = "";
          }
          if(ask === 0){
            ask = "";
          }
          ords.push({
            "Strike": strike,
            "Bid": bid,
            "Ask": ask
          });
        }
        this.setState({
          orders: ords,
          ready: true
        });
      }else{
         var ords = [];
        this.setState({
          orders:ords,
          ready:false
        })
      }
      })
  }

  componentWillUnmount(){
    this.aggobRef.off();
  }

  render() {
    const quips = ["Calling in favors...", "Embezzling Funds...", "Betting against USD...", "Misplaced your cash..", "On the phone with the SEC..."];
    const style ={
      position: 'sticky',
      top: '0px',
      zIndex:100,
      background:'white'
    }
  return(
    <div id='PlaceOrder'>
    <div style={style}>
    <MarketOrders uid={this.props.uid} sessionid={this.props.sessionid} />
    <Header />
    </div>

    {
    this.state.ready
    ? <div className='row'>
      {this.state.orders.reverse().map(function(ord, index){
        //console.log(this.state.orders);
        return(
        <div>
          <OrderBookEntry uid={this.props.uid} sessionid={this.props.sessionid} Bid={ord.Bid} Strike={ord.Strike} Ask={ord.Ask} />
          {/* <h4>${ord.Strike}</h4>
          <OrderBookEntry Bid={ord.Bid} Strike={ord.Strike} Ask={ord.Ask} type="Ask"/> */}
        </div>
        );

    }.bind(this))}


  </div>
  :<div>{quips[Math.floor(Math.random()*quips.length)]}</div>
}
</div>
    );
  }
}
export default PlaceOrder;



