import React, { Component } from 'react';
import Button from './Buttons.js'
import SessionContainer from './Sessions/SessionContainer.js';
import PlaceOrder from './Orderbook/PlaceOrder'
import '../../node_modules/bootstrap/dist/css/bootstrap.min.css';
import * as firebase from 'firebase';


class OAuth extends Component {

  constructor(props){
    super(props);
    this.state = {
      buttonVisible: true
    }
  }

  componentWillMount(){
    firebase.auth().onAuthStateChanged((user)=>{
      if(user){
        //user is signed in
        this.authHandler(user);
      }else{
        console.log("not signed in");
      }
    });
  }

  authenticate(provider){
    console.log("trying to login with "+provider);
    firebase.auth().signInWithRedirect(provider)
  }

  authHandler(authData){
    if(authData != null){
      this.setState({
        buttonVisible: false
      });
    }else{

    }
  };

  signout(){
    firebase.auth().signOut()
      .then(function(){
        this.setState({
          buttonVisible: true
        })
      }.bind(this))
  }

  render() {
    return (
    <div>
    <div style={{'float': 'right', 'marginTop': '14.25px', 'marginRight': '20px'}}>
      {
        this.state.buttonVisible
          ? <button onClick={this.authenticate.bind(this, new firebase.auth.GoogleAuthProvider())} className="btn-primary google">Login with Google</button>
          : <button onClick={this.signout.bind(this)} className="btn-danger logout">Logout</button>
        }
    </div>
  </div>
    );
  }
}
export default OAuth;



