import React, { Component } from 'react';
import Session from './Session';
import {Link} from 'react-router-dom';
import '../../style/SessionContainer.css';
import * as firebase from 'firebase';

class SessionContainer extends Component {


  constructor(props){
    super(props);
    this.state = {
      sessions: []
    };
  }


  componentDidMount(){
    //need to do this
    var sessionid, title, users;
    var self = this;
    this.SessionsRef = firebase.database().ref("PlayableSessions");
    this.SessionsRef.on('value', snap =>{
      var rawData = snap.val();
      //console.log(rawData);
      var sesses = [];
      for(var key in rawData){
        sessionid = key;
        //console.log(sessionid);
        // institution = rawData[key].Institution;
        // //console.log(institution);
        // profname = rawData[key].ProfessorName;
        //console.log(profname);
        title = rawData[key].Title;
        //console.log(title);
        //users = rawData[key].UserCount;
        //console.log(users);

        sesses.push({
          "SessionId": sessionid,
          // "Institution": institution,
          // "ProfessorName": profname,
          "Title": title,
          //"Users": users
        });
      }
      //console.log("before1");
       self.setState({
         sessions: sesses
       });
       //console.log("after1")
     })
  }

  componentWillUnmount(){
    this.SessionsRef.off();
  }

  render() {

    const style = {
      marginLeft: '20px'
    };

  return(
    <div className="SessionContainer">
      <Link to={{
          pathname: '/AdminHome',
          state: { sessionid: this.props.sessionid }
        }}><button style={style} className="btn-primary google">Admin Homepage</button></Link>
    <div className='row'>

      {this.state.sessions.reverse().map(function(sess, index){

        return(
        <div>
          <Session user={this.props.user} uid={this.props.user.uid}  sessionid={sess.SessionId}
           title={sess.Title} />
        </div>
        );

    }.bind(this))}
  </div>
</div>
    );
  }
}
export default SessionContainer;



