import React, { Component } from 'react';
import * as firebase from 'firebase';

class Stats extends Component {

  constructor(props){
    super(props);
    this.state = {
      AverageBuyPrice: '',
      AverageSellPrice:'',
      MarketVWAP:''
    };
  }

  componentDidMount(){
    this.statsRef = firebase.database().ref("Sessions").child(this.props.sessionid).child("Users").child(this.props.uid).child("Stats");
    this.vwapRef = firebase.database().ref("Sessions").child(this.props.sessionid).child("Assets").child("INTC").child("VWAP");

     this.statsRef.on('value', snap =>{
       if(snap.val()){
         this.setState({
           AverageBuyPrice : snap.val().BuyVWAP,
           AverageSellPrice: snap.val().SellVWAP
         });
       }else{
         this.setState({
           AverageBuyPrice : '',
           AverageSellPrice : ''
         });
       }
     });

     this.vwapRef.on('value', snap => {
       if(snap.val()){
         this.setState({
           MarketVWAP:snap.val()
         });
       }else{
         this.setState({
           MarketVWAP:''
         });
       }
     });
  }

  componentWillUnmount(){
    this.statsRef.off();
    this.vwapRef.off();
  }

  render() {
    return (
      <div>
      <table className="table table-bordered">
              <thead>
                <tr>
                  <td>AverageBuyPrice</td>
                  <td>AverageSellPrice</td>
                  <td>MarketVWAP</td>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>{this.state.AverageBuyPrice}</td>
                  <td>{this.state.AverageSellPrice}</td>
                  <td>{this.state.MarketVWAP}</td>
                </tr>
              </tbody>
            </table>
          </div>
    );
  }
}
export default Stats;



