import React, { Component } from 'react';
//import OAuth from './OAuth';
import * as firebase from 'firebase';

class Button extends Component {

constructor(){
  super();
  this.state = {
    isPaused: true,
    ready: false
  }
}

start(){
  const simStateRef = firebase.database().ref().child("Sessions").child(this.props.sessionid).child('StartSim').child('start');
  this.setState({
    isPaused: false
  })
  //console.log("true");
  simStateRef.set(true);
}

// pause(){
//   const simStateRef = firebase.database().ref().child("Sessions").child(this.props.sessionid).child('StartSim').child('start');
//   this.setState({
//     isPaused: true
//   })
//   //console.log("false");
//   simStateRef.set(false);
// }

componentWillMount(){
  this.buttonStateRef = firebase.database().ref().child("Sessions").child(this.props.sessionid).child('StartSim').child('Ready');
  this.buttonStateRef.on('value', snap => {
    if(snap.val()){
      this.setState({
        ready: true
      })
    }
  })
}

componentWillUnmount(){
  this.buttonStateRef.off();
}

  render() {
    const isPaused = this.state.isPaused;
    let button = null;
    if (isPaused) {
      button = <button onClick={this.start.bind(this)} className="btn-basic start">Start Sim</button> ;
    } else {
      button = null; //<button onClick={this.pause.bind(this)} className="btn-basic pause">Pause</button>;
    }

    return (
      <div className="col-md-2">
      {
        this.state.ready && isPaused
          ? button
          : <div></div>
      }
      </div>
    );
  }
}

export default Button;



