// service
import React, { Component } from 'react';
import firebase from 'firebase';
import { BrowserRouter as Router, Route, Redirect } from 'react-router-dom';
//import './style/App.css';

// page component
import Header from './components/Header';
import SessionContainer from './components/Sessions/SessionContainer.js';
import AdminSessionContainer from './components/Admin/AdminSessionContainer.js';
import StudentsStats from './components/Admin/StudentsStats.js';
import AdminHome from './components/Admin/AdminHome.js';
import CreateSession from './components/Admin/CreateSession.js';
import Container from './components/Orderbook/Container.js';

const Home = () => (
  <div>
    <h2>Home</h2>
    <p>Please sign-in with your UDel email using the button in the top right corner</p>
  </div>
)

const renderMergedProps = (component, ...rest) => {
  const finalProps = Object.assign({}, ...rest);
  return (
    React.createElement(component, finalProps)
  );
}

const PrivateRoute = ({component: Component, authed, ...rest}) => (
  <Route {...rest}
    render={(props) => authed === true
      ? (renderMergedProps(Component, props, rest))
      : <Redirect to={
        {pathname: '/',
        state: {from: props.location}
        }} />}
  />
)

class App extends Component {
  constructor () {
    super()
    this.state = {
      shouldRedirect: false,
      redirectPath: "",
      authed: false,
      user: {}
    }
  }
  componentDidMount () {
    this.userStateChange = firebase.auth().onAuthStateChanged((user) => {
      if (user) {
        this.setState({
          shouldRedirect: true,
          redirectPath: "/Home",
          authed: true,
          user: user
        })
        //console.log(this.state.user.uid);
      } else {
        this.setState({
          shouldRedirect: true,
          redirectPath: "/",
          authed: false,
          user: {}
        })
        console.log("logged out")
      }

    })
  }

  componentWillUnmount () {
    this.userStateChange()
  }

  render() {
    return (
      <Router>
        <div>
          <Header user={this.state.user} />
              <div className="App">
                <Route exact path="/" component={Home}/>
                {/* <PrivateRoute authed={this.state.authed} path="/CreateSession" component={CreateSession} /> */}
                <PrivateRoute authed={this.state.authed} path="/AdminHome" user={this.state.user} component={AdminHome} />
                {/* i think i need to pass user object here */}
                <PrivateRoute authed={this.state.authed} path="/Home" user={this.state.user}  component={SessionContainer} />
                <PrivateRoute authed={this.state.authed} path="/AdminSessionContainer" uid={this.state.user.uid}  component={AdminSessionContainer} />
                <PrivateRoute authed={this.state.authed} path="/Stats" user={this.state.user}  component={StudentsStats} />
                <PrivateRoute authed={this.state.authed} path="/Simulation" uid={this.state.user.uid} component={Container} />
                {/* <PrivateRoute authed={this.state.authed} path="/AdminSimulation" component={Container} /> */}
                {this.state.shouldRedirect && (
                  <Redirect to={this.state.redirectPath} />
                )}
              </div>
        </div>
      </Router>
    );
  }
}

export default App;



