import React, { Component } from 'react';
import Asset from './Assets';
import Stats from './Stats';
import PlaceOrder from './PlaceOrder'
import Transaction from './Transactions';
import Switcher from '../Charts/Switcher';
import '../../style/Orderbook.css';
import '../../../node_modules/bootstrap/dist/css/bootstrap.min.css';
import * as firebase from 'firebase';

class Container extends Component {

  render() {
    return (
      <div className="container">
        <div className="row">
            <div id="orderbook_test" className="col-md-5">
                <PlaceOrder sessionid={this.props.location.state.sessionid} uid={this.props.uid} />
            </div>

              <div id="assets" className="col-md-2">
                <Asset sessionid={this.props.location.state.sessionid} uid={this.props.uid} />
              </div>

              <div id="stats" className="col-md-2">
                <Stats sessionid={this.props.location.state.sessionid} uid={this.props.uid} />
              </div>
              <div id="plotly-div" className="col-md-3">
                <Switcher sessionid={this.props.location.state.sessionid} />
              </div>
          </div>
          <div className="row">
              <div className="col-md-12" id="transactions">
                <Transaction sessionid={this.props.location.state.sessionid} uid={this.props.uid} />
              </div>
          </div>
      </div>
    );
  }
}

export default Container;



