import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import '../../style/Session.css';
import * as firebase from 'firebase';

class Session extends Component {



  constructor(props){
    super(props);
    this.state = {
      ready: false
    };
  }

  joinSession(){

    const UserPendingRef = firebase.database().ref().child("Sessions").child(this.props.sessionid).child("UsersPending").child(this.props.user.uid);

    let user = {
        uid: this.props.user.uid,
        displayName: this.props.user.displayName // should be user, talk to jackson about handling that
    }
    UserPendingRef.update(JSON.parse(JSON.stringify(user)));
  }

  deleteSession(){
    this.StartRef.off();
    const playableRef = firebase.database().ref("PlayableSessions").child(this.props.sessionid);
    playableRef.remove();
    const SessionIDRef = firebase.database().ref("Sessions").child(this.props.sessionid);
    SessionIDRef.remove();
    //window.location.reload(); //temporary fix
  }

  handleChange(){
    var newTitle = this.refs.newTitle.innerHTML;
    const titleRef = firebase.database().ref("Sessions").child(this.props.sessionid).child("Info").child("Title");
    const playableRef = firebase.database().ref("PlayableSessions").child(this.props.sessionid).child("Title");
    titleRef.set(newTitle);
    playableRef.set(newTitle);
    //console.log("blur worked");
  }


  componentDidMount(){
  this.StartRef = firebase.database().ref("Sessions").child(this.props.sessionid).child("StartSim").child("Ready");
  this.StartRef.on('value', snap=>{
    //console.log("ready state", this.state.ready, snap.val());
    if(snap.val()){
      setTimeout(function(){
        this.setState({
          ready: true
        });
      }.bind(this),3000);
    }
    // else{
    //   console.log("else excecuted correctly");
    //   this.setState({
    //     ready:false
    //   });
    //   console.log("in else, setState called");
    // }
    // else if(!snap.val()){
    //   this.setState({
    //     ready:false
    //   });
    // }
    //console.log("state after if", this.state.ready, this.props.sessionid);
  })
}

componentWillUnmount(){
  this.StartRef.off();
}



  render(){
    const style = {
      border: '1px black solid',
      borderRadius: '5px',
      padding: '15px',
      margin: '5px',
      marginLeft: '30px',
      width:'40%'
    };

    return (
      <div>
        <div className='row' style={style}>
          {/* <div className='col-md-2'>{this.props.institution}</div>
          <div className='col-md-2'>{this.props.profname}</div> */}
          <div onBlur={this.handleChange.bind(this)} contentEditable="true" ref="newTitle" className='col-md-5'>{this.props.title}</div>
          {/* <div className='col-md-1'>{this.props.users}/24</div> */}
          {
            this.state.ready
            ?  <Link to={{
              pathname: '/Simulation',
              state: { sessionid: this.props.sessionid }
            }}><div className='col-md-2'><button onClick={this.joinSession.bind(this)} className="btn-basic">Join</button></div></Link>
            :<div className='col-md-2'>...</div>
          }
            <div className='col-md-2'><button onClick={this.deleteSession.bind(this)} className="btn-basic">Delete</button></div>
        </div>
      </div>
    );
  }
}
export default Session;



