import React, { Component } from 'react';
import firebase from 'firebase';


class Transaction extends Component {

  constructor(props){
    super(props);
    this.state = {
      transactions: []
    };
  }

// parameters per transaction:
// Time:'',
// Volume:'',
// AssetId:'',
// Type:'',
// Price:''

  componentDidMount(){
    this.transRef = firebase.database().ref("Sessions").child(this.props.sessionid).child("Users").child(this.props.uid).child("Transactions");
//need to refactor based on new transactions
     this.transRef.on('value', snap =>{
       var data = snap.val();
       //console.log(data);
       var trns = [];
       for(var key in data){
         if (data.hasOwnProperty(key)){
           trns.push(data[key]);
         }
       }

       this.setState({
         transactions: trns
       });

     });
  }

  componentWillUnmount(){
    this.transRef.off();
  }

  render() {
    const style = {
    height: '300px',
    overflow: 'auto',
    border: '1px solid black',
    padding:'2%'

  };
    //console.log(this.state.transactions, "in render");
    return (
      <div className='row' style={style}>
        {this.state.transactions.reverse().map(function(trn, index){
          if(trn.Type === "Ask"){
            trn.Type = "Sold";
          }else if(trn.Type === "Bid"){
            trn.Type = "Bought";
          }
          return (
          <div>
             <div className='col-xs-3 col-md-offset-1'>{trn.Time}</div>
             <div className='col-xs-2'>{trn.Volume} shares of</div>
             <div className='col-xs-2'>ABC were</div>
             <div className='col-xs-1'>{trn.Type} at</div>
             <div className='col-xs-2'>${(trn.Price/100).toFixed(2)}</div>
           </div>);
        })}
      </div>
    );
  }
}
export default Transaction;



