import React, { Component } from 'react';
import {Redirect} from 'react-router-dom';
import * as firebase from 'firebase';

class CreateSession extends Component {
  constructor(props) {
    super(props);
    this.state = {
      "Institution": '',
      "ProfessorName": '',
      "Title": '',
      "StartPrice": 21.00,
      "UserCount": 0,
      "msPerSec": 15,
      "numLiq": 100,
      "numInf": 50,
      "numMom": 50,
      "numPStar": 3,
      "FracOfDay": 1,
      "Submitted": false
    };

    this.handleInputChange = this.handleInputChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.standardGame = this.standardGame.bind(this);
  }

  handleInputChange(event) {
    const sessionRef = firebase.database().ref("Sessions");
    //console.log(sessionref.push().key);
    //TODO: might be better to check for whole number here
    const target = event.target;
    const value = target.value;
    const name = target.name;

    this.setState({
      //SessionId: sessionref.push(),
      [name]: value
    });
  }

  handleSubmit(event) {
  const sessionRef = firebase.database().ref("Sessions");
   //alert('A form was submitted');
   event.preventDefault();
   var newSessionRef = sessionRef.push();
   var session = {
     "Institution": this.state.Institution,
     "ProfessorName": this.state.ProfessorName,
     "Title": this.state.Title,
     "UserCount": 0,
     "GameParams": {
        "StartPrice": (this.state.StartPrice*100).toFixed(0),
         "msPerSec": parseInt(this.state.msPerSec),
         "numLiq": parseInt(this.state.numLiq),
         "numInf": parseInt(this.state.numInf),
         "numMom": parseInt(this.state.numMom),
         "numPStar": parseInt(this.state.numPStar),
         "FracOfDay": parseInt(this.state.FracOfDay)
     },
     "StartSim":{
       "SessionID": newSessionRef.key
     }
   }
   if(this.state.msPerSec % 1 === 0 && this.state.msPerSec > 0 && this.state.msPerSec <= 30){
   newSessionRef.set(session);
   this.setState({
     Submitted: true
   })
 }else{
   alert("You must input an integer between 0 and 30 here");
  }

 }

 standardGame(event) {
   document.getElementById("StartPrice").value="21.00";
   document.getElementById("msPerSec").value="15";
   document.getElementById("numLiq").value="100";
   document.getElementById("numInf").value="50";
   document.getElementById("numMom").value="50";
   document.getElementById("numPStar").value="3";
   document.getElementById("FracOfDay").value="1";
}

  render() {
    return (
      <form onSubmit={this.handleSubmit}>
        <label>
          Institution:
          <input
            name="Institution"
            type="text"
            required
            value={this.state.value}
            onChange={this.handleInputChange} />
        </label>
        <br />
        <label>
          Instructor:
          <input
            name="ProfessorName"
            type="text"
            required
            value={this.state.value}
            onChange={this.handleInputChange} />
        </label>
        <br />
        <label>
          Title:
          <input
            name="Title"
            type="text"
            required
            value={this.state.value}
            onChange={this.handleInputChange} />
        </label>
        <br />
        <label>
          Starting Price:
          <input
            name="StartPrice"
            id="StartPrice"
            type="text"
            required
            value={this.state.value}
            onChange={this.handleInputChange} />
        </label>
        <br />
        {/* <label>
          AssetsId:
          <input
            name="Title"
            type="text"
            value={this.state.value}
            onChange={this.handleInputChange} />
        </label>
        <br /> */}
        <label>
          Ms/s:
          <input
            id="msPerSec"
            name="msPerSec"
            type="text"
            required
            value={this.state.value}
            onChange={this.handleInputChange} />
        </label>
        <br />
        <label>
          Number of Liquidity:
          <input
            id="numLiq"
            name="numLiq"
            type="text"
            required
            value={this.state.value}
            onChange={this.handleInputChange} />
        </label>
        <br />
        <label>
          Number of Informed:
          <input
            id="numInf"
            name="numInf"
            type="text"
            required
            value={this.state.value}
            onChange={this.handleInputChange} />
        </label>
        <br />
        <label>
          Number of Momentum:
          <input
            id="numMom"
            name="numMom"
            type="text"
            required
            value={this.state.value}
            onChange={this.handleInputChange} />
        </label>
        <br />
        <label>
          Number of P* Changes:
          <input
            id="numPStar"
            name="numPStar"
            type="text"
            required
            value={this.state.value}
            onChange={this.handleInputChange} />
        </label>
        <br />
        <label>
          Fraction of Day:
          <input
            id="FracOfDay"
            name="FracOfDay"
            type="text"
            required
            value={this.state.value}
            onChange={this.handleInputChange} />
        </label>
        <br />
        <input type="submit" value="Submit" />
        <button type="button" onClick={this.standardGame}>Standard Game</button>
        {this.state.Submitted && (<Redirect to="/Home" />)}
      </form>

    );
  }
}
export default CreateSession;



