import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import '../../style/Session.css';
import * as firebase from 'firebase';

class StudentsStats extends Component {

  constructor(props){
    super(props);
    this.state = {
      students: [],
      user: this.props.user,
      MarketVWAP: ''
    };
    //this.props.sessionid = this.props.location.state.sessionid;
  }


  componentDidMount(){
    const sessionStatsRef = firebase.database().ref("Sessions").child(this.props.location.state.sessionid).child("Users");
    this.vwapRef = firebase.database().ref("Sessions").child(this.props.location.state.sessionid).child("Assets").child("INTC").child("VWAP");
    var username, shares, cash, BuyVWAP, SellVWAP, MarketVWAP;

    this.vwapRef.on('value', snap => {
      if(snap.val()){
        this.setState({
          MarketVWAP:snap.val()
        });
      }else{
        this.setState({
          MarketVWAP:''
        });
      }
    });

    sessionStatsRef.on('value', snap => {
      var rawData = snap.val();
      //console.log(rawData);
      var stdnts = [];
      for(var key in rawData){
        if(key != 0){
        username = rawData[key].DisplayName;
        shares = rawData[key].Assets.INTC;
        cash = rawData[key].Assets.Cash;
        BuyVWAP = rawData[key].Stats.BuyVWAP;
        SellVWAP = rawData[key].Stats.SellVWAP;
        MarketVWAP = this.state.MarketVWAP;

        stdnts.push({
          //need to read user object i think
          "User":  username,
          "Shares": shares,
          "Cash": cash,
          "AverageBuyPrice": BuyVWAP,
          "AverageSellPrice": SellVWAP
          // "MarketVWAP": MarketVWAP
          });
        }
      }
      this.setState({
        students: stdnts
      });
      //console.log(this.state.students);
    })
  }

  render(){
    const style = {
      border: '1px black solid',
      borderRadius: '5px',
      padding: '15px',
      margin: '5px',
      width:'80%'
    };

    return (
      <div>
      <div style={{"textAlign":"center", "fontSize":"20px"}} className='row'>Market VWAP: {this.state.MarketVWAP} </div>
      <div>
        {this.state.students.map(function(stdnt, index){
          return(
        <div className='row' style={style}>

          <div className='col-md-2'>User: {stdnt.User}</div>
          {/* <div className='col-md-1'>Position: </div> */}
          <div className='col-md-2'>ABC: {stdnt.Shares}</div>
          <div className='col-md-2'>Cash: {stdnt.Cash}</div>
          <div className='col-md-3'>Average Buy Price: {stdnt.AverageBuyPrice}</div>
          <div className='col-md-3'>Average Sell Price: {stdnt.AverageSellPrice} </div>
          {/* <div className='col-md-2'>MarketVWAP: {stdnt.MarketVWAP} </div> */}

        </div>
        );})}
      </div>
    </div>
    );
  }
}
export default StudentsStats;



