import React, { Component } from 'react';
//import SessionContainer from '../Sessions/SessionContainer';
import AdminSessionContainer from './AdminSessionContainer';
import Button from '../Buttons.js';
import {Link} from 'react-router-dom';
import '../../style/SessionContainer.css';
import * as firebase from 'firebase';

class AdminHome extends Component {


  constructor(props){
    super(props);

  }

  createSession(){
    const sessionRef = firebase.database().ref("Sessions");
     var newSessionRef = sessionRef.push();
     var timeCreated = new Date().toUTCString();
     var session = {
       //"Title": this.props.user.displayName + timeCreated,
       "UserCount": 0,
       "GameParams": {
          "StartPrice": "2100", //(this.state.StartPrice*100).toFixed(0),
           "msPerSec": 15,
           "numLiq": 100,
           "numInf": 50,
           "numMom": 50,
           "numPStar": 3,
           "FracOfDay": 1
       },
       "StartSim":{
         "SessionID": newSessionRef.key
       }
     }
     newSessionRef.set(session);
     newSessionRef.child("Info").child("Title").set(this.props.user.displayName + timeCreated);
     //<a href="javascript:window.open('','_blank').close();"></a>
     //window.open("/AdminHome"); //needs sessionid
  }


  componentDidMount(){

  }

  render() {

    const style = {
      marginTop:'5px',
      marginLeft: '20px'
    };

  return(
    <div>
    {/* <Link to="/CreateSession">*/}<Link to="/Home"><button onClick={this.createSession.bind(this)} style={style} className="btn-primary google">Create a Session</button></Link>{/*</Link> */}
    {/* <Button sessionid={this.props.location.state.sessionid} /> */}
    <AdminSessionContainer />
  </div>
    );
  }
}
export default AdminHome;



