import React, { Component } from 'react';
import * as firebase from 'firebase';

class Asset extends Component {

  constructor(props){
    super(props);
    this.state = {
      Cash: '',
      ABC:''
      //MSFT:''
    };
  }

  componentDidMount(){
    this.assetsRef = firebase.database().ref("Sessions").child(this.props.sessionid).child("Users").child(this.props.uid).child("Assets");

     this.assetsRef.on('value', snap =>{
       if(snap.val()) {
         this.setState({
           Cash : snap.val().Cash,
           ABC : snap.val().INTC
          //  MSFT : snap.val().MSFT
         });
       } else {
         this.setState({
           Cash : '',
           ABC : ''
           //MSFT : ''
         });
       }
     });
  }

  componentWillUnmount(){
    this.assetsRef.off();
  }

  render() {
    return (
      <div>
      <table  className="table table-bordered">
              <thead>
                <tr>
                  <td>Cash</td>
                  <td>ABC</td>
                  {/* <td>MSFT</td> */}
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>{this.state.Cash}</td>
                  <td>{this.state.ABC}</td>
                  {/* <td>{this.state.MSFT}</td> */}
                </tr>
              </tbody>
            </table>
          </div>
    );
  }
}
export default Asset;



