import React, { Component } from 'react';
import '../../../node_modules/bootstrap/dist/css/bootstrap.min.css';
import Countdown from './Countdown';
import Button from '../Buttons';
import * as firebase from 'firebase';

class MarketOrders extends Component {
  handleMarketKeyUp = (event) => {
    const ordersPendingRef = firebase.database().ref("Sessions").child(this.props.sessionid).child("Users").child(this.props.uid).child("OrdersPending");
      if(event.key === 'Enter'){
        if(event.target.value != null && !isNaN(event.target.value) && event.target.value % 1 === 0){
          var volumeInt = parseInt(event.target.value);
          if(volumeInt > 0 && volumeInt < 999){
            if(event.target.name==='marketBid'){
              var order = {
                Type: "Bid",
                Volume: volumeInt,
                UserId: this.props.uid,
                AssetId: "INTC",
                Strike: null,
                Class: "Market"

              }
              //console.log(order);
              var newOrderRef = ordersPendingRef.push();
              newOrderRef.set(order);
              event.target.value = "";
            }else{

              order = {
                Type: "Ask",
                Volume: volumeInt,
                AssetId: "INTC",
                UserId: this.props.uid,
                Strike: null,
                Class: "Market"
              }
              //console.log(order);
              newOrderRef = ordersPendingRef.push();
              newOrderRef.set(order);
              event.target.value = "";
            }
          }else{
            alert("You must enter a number between 0 and 999");
            event.target.value = "";
          }
        }else{
          alert("You must enter a valid whole number");
          event.target.value = "";
        }
    };
  };
  render() {

    return (
      <div className='row'>
        <div className='col-md-4'>Market Buy:<input onKeyUp={this.handleMarketKeyUp} className="marketBid" type="text" name="marketBid" size="4" /></div>
        <Button sessionid={this.props.sessionid} />
        <Countdown sessionid={this.props.sessionid} />
        <div className='col-md-4' >Market Sell:<input onKeyUp={this.handleMarketKeyUp} className="marketAsk" type="text" name="marketAsk" size="4" /></div>
      </div>
    );
  }
}
export default MarketOrders;



